.cart__box{
    display: flex;
    column-gap: 20px;
    row-gap:20px;
}

.cart__col{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.cart__col:nth-child(1){
    width: 35%;
}

.cart__col:nth-child(2){
    width: 45%;
}

.cart__col:nth-child(3){
    width: 20%;
    align-items: center;
}

.cart__col:nth-child(1) img{
    width: 100%;
    height: 100px;
}

.cart__col:nth-child(2) p{
    font-size: 14px;
}

.cart__delete{
    cursor: pointer;
}
.cart__quantity{
    color:brown;
}

.cart__count{
    display: flex;
    align-items: center;
    column-gap: 10px;
}

.cart__count button{
    padding: 5px 10px;
    cursor: pointer;
}