#main{
    padding: 150px 0 100px;
}

#main h2{
    text-align: center;
    margin:30px 0;
}

.main__box{
    display: flex;
    background: #fff;
    border-radius: 20px;
    flex-direction: column;
    row-gap:10px;
    padding: 20px;
    box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
}

.main__box img{
    width: 100%;
    height: 150px;
    border-radius: 20px;
}

.main__box p:nth-child(2){
    font-weight: bold;
}

.main__box p:nth-child(3){
    font-size: 20px;
}