*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


a {
  text-decoration: none;
}

.container {
  max-width: 80%;
  margin: 0 auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  row-gap: 30px;
  margin: 0 -15px;
}

body{
  background: #F9F6EE;
}

.col-3 {
  width: 25%;
  padding: 0 15px;
}


.col-6 {
  width: 50%;
  padding: 0 15px;
}